import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/card-button.css';
import {Link} from '@vkontakte/vkui';

export class CardButton extends PureComponent {

    render() {
        const ret = this.props.hasOwnProperty('link') ?
            <Link href={this.props.link} target="_blank">
                <div className={ "card-button" } style={{ background: this.props.background }}>
                    <div className="inner">
                        <img
                            className="img"
                            src={ this.props.before }>
                        </img>
                        <div className="other">
                            <div className="title">{ this.props.title }</div>
                            <div className="desc">{ this.props.desc }</div>
                        </div>
                    </div>
                </div>
            </Link>
            :
            <div className={ "card-button" } onClick={this.props.onClick} style={{ background: this.props.background }}>
                <div className="inner">
                    <img
                        className="img"
                        src={ this.props.before }>
                    </img>
                    <div className="other">
                        <div className="title">{ this.props.title }</div>
                        <div className="desc">{ this.props.desc }</div>
                    </div>
                </div>
            </div>;

        return (
            ret
        )
    }

}